<template>
  <div class="style">
    <div  v-html="renderStyle"></div>
  </div>
</template>

<script>
export default {
  props:['styleF'],
  computed:{
    renderStyle:function(){
      return `<style>${this.styleF}</style>`
    }
  }
}
</script>



